.app__about {
    flex: 1;
    width: 100%;
    flex-direction: column;
}

.app__about-text{
    font-size: 1rem;
    text-align: left;
    color: var(--gray-color);
    line-height: 1.5;
    text-align: center;
    width: 50%;
  
    @media screen and (min-width: 2000px) {
      font-size: 2rem;
    }

    @media screen and (max-width: 450px) {
        width: 100%;
    }
}


.app__profiles {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 2rem;
}

.app__profile-item {
    width: 190px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin: 2rem;

    img {
        width: 100%;
        height: 170px;
        border-radius: 15px;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
        width: 370px;
        margin: 2rem 4rem;
        img {
            height: 320px;
        }
    }
}